import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3 } from '../../../../utils/plot'
import { ms_per_day, len, down_sample } from '../../../../mods/a_utils'

export default function CombinedResourcePlot({
  optimizer,
  result,
  width = 560,
  height = 500,
  keep = 1000,
}) {
  const ref = useRef()
  const combined_dist = result?.rsrc_dist
  console.log(optimizer)

  useEffect(() => {
    if (!combined_dist) return

    const data = down_sample(Object.entries(combined_dist), keep)
    const color = d3.scaleOrdinal(d3.schemeCategory10)

    const x_optimized_domain = d3.extent(Object.keys(combined_dist))
    const x_baseline_domain = d3.extent(Object.keys(optimizer.baseline_result?.rsrc_dist))
    const x_domain = [
      Math.min(x_optimized_domain[0], x_baseline_domain[0]),
      Math.max(x_optimized_domain[1], x_baseline_domain[1]),
    ]

    const resources = result.inp.rsrcs
    for (const r in resources) {
      const y_domain = d3.extent(Object.values(resources[r].dist))

      const { svg, x_scale, y_scale } = init_d3({
        x_domain,
        y_domain,
        width,
        height,
        ref,
        // x_label: 'time',
        // y_label: 'units',
        x_type: d3.scaleUtc,
        y_type: d3.scaleLinear,
      })

      const rect = svg.append('g')

      rect
        .selectAll('rect')
        .data(data)
        .join('rect')
        .attr('x', (d) => x_scale(d[0]))
        .attr('width', x_scale(ms_per_day) - x_scale(0))
        .attr('y', (d) => y_scale(d[1]))
        .attr('height', (d) => y_scale(y_domain[0]) - y_scale(d[1]))
        .attr('fill', (d, i) => {
          var colors = ['#f7dc5b98', '#82B5C798', '#AABB5D98', '#db793098', '#099A4D98']
          //  '#B21071',
          return colors[i % colors.length]
        })
    }
  }, [combined_dist])
  
  // useEffect(() => {
  //   if (!combined_dist) return

  //   const data = down_sample(Object.entries(combined_dist), keep)
  //   const color = d3.scaleOrdinal(d3.schemeCategory10)

  //   const x_optimized_domain = d3.extent(Object.keys(combined_dist))
  //   const x_baseline_domain = d3.extent(Object.keys(optimizer.baseline_result?.rsrc_dist))
  //   const x_domain = [
  //     Math.min(x_optimized_domain[0], x_baseline_domain[0]),
  //     Math.max(x_optimized_domain[1], x_baseline_domain[1]),
  //   ]

  //   const y_optimized_domain = d3.extent(Object.values(combined_dist))
  //   const y_baseline_domain = d3.extent(Object.values(optimizer.baseline_result?.rsrc_dist))
  //   const y_domain = [
  //     Math.min(y_optimized_domain[0], y_baseline_domain[0]),
  //     Math.max(y_optimized_domain[1], y_baseline_domain[1]),
  //   ]

  //   const { svg, x_scale, y_scale } = init_d3({
  //     x_domain,
  //     y_domain,
  //     width,
  //     height,
  //     ref,
  //     // x_label: 'time',
  //     // y_label: 'units',
  //     x_type: d3.scaleUtc,
  //     y_type: d3.scaleLinear,
  //   })

  //   const rect = svg.append('g')

  //   rect
  //     .selectAll('rect')
  //     .data(data)
  //     .join('rect')
  //     .attr('x', (d) => x_scale(d[0]))
  //     .attr('width', x_scale(ms_per_day) - x_scale(0))
  //     .attr('y', (d) => y_scale(d[1]))
  //     .attr('height', (d) => y_scale(y_domain[0]) - y_scale(d[1]))
  //     .attr('fill', (d, i) => {
  //       var colors = ['#f7dc5b98', '#82B5C798', '#AABB5D98', '#db793098', '#099A4D98']
  //       //  '#B21071',
  //       return colors[i % colors.length]
  //     })

  // }, [combined_dist])

  if (!combined_dist) return null
  const msg =
    len(combined_dist) > keep
      ? `(showing ${keep}/${len(combined_dist)} only due to limited pixels)`
      : ''
  return (
    <>
      {/* <Title>{title}</Title> */}
      <>{msg}</>
      <svg ref={ref}></svg>
    </>
  )
}